import axios from '@axios';

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    fetchQuotas(ctx, queryParams) {
      return new Promise((resolve, reject) => {
        axios
          .get('/quota', { params: queryParams })
          .then((response) => {
            resolve(response);
          })
          .catch((error) => reject(error));
      });
    },
    fetchQuota(ctx, { id }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/quota/${id}`)
          .then((response) => resolve(response))
          .catch((error) => reject(error));
      });
    },
    fetchClients() {
      return new Promise((resolve, reject) => {
        axios
          .get('/customer')
          .then((response) => resolve(response))
          .catch((error) => reject(error));
      });
    },
  },
};
